*{margin:0;padding:0;font-family:"Shippori Mincho", serif;box-sizing:border-box}.project-page{margin-top:50px;display:-webkit-inline-flex;display:inline-flex}.project-page h3,.project-page h4{text-align:center;padding-left:10px;margin-left:0;width:300px}.project-page .btntext{width:70px;display:-webkit-flex;display:flex;margin-left:120px}.project-page .btntext button{width:100px;display:center}.project-page .negative{padding:0.5px 0.5px -0.5px 0.5px;text-align:center}.image img{transition:2s;height:auto}.image:hover img{-webkit-transform:scale(1.5);transform:scale(1.5)}

